import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import AsyncComputed from 'vue-async-computed'
import './assets/style.css'

import MuseUI from '@/components/mat';

import './assets/css/semantic/semantic.scss'

window.scrollToBottom = () => {
    console.log("dsds")
    window.scrollTo(0, document.body.scrollHeight)
}

Vue.filter('wrap', function (value, begin, end) {
    return begin + value + end
})

const replace = (matchedTxt, a, b) => {
    return (`<span class="highlight">${matchedTxt}</span>`);
}

Vue.filter('highlight', (value, query) => {
    if (query === null || query.length === 0) return value;
    let iQuery = new RegExp(query, "ig");
    return value.toString().replace(iQuery, replace);
});

Vue.config.productionTip = false
Vue.use(MuseUI);
Vue.use(AsyncComputed);

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
