<template>
  <sui-segment
      :style="`border-left: 5px solid ${log.color} !important;`"
      :class="`${index % 2 === 0 ? 'even' : 'odd'}`"
  >
    <div>
      <time>{{ formatTimestamp(log) }}</time>
      <span class="logFileName">{{ fileName(log.fileName) }}</span>
      <span class="logFunction">.{{ log.function }}</span>
      <span class="logLine"> :{{ log.line }}</span>
    </div>

    <div class="code-text">method:
      <span v-html="$options.filters.highlight(jsonLog.method, $store.state.search)"></span>
    </div>
    <div class="code-text" style="margin-left: 1em;">url:
      <span v-html="$options.filters.highlight(jsonLog.url, $store.state.search)"></span>
    </div>
    <div class="code-text">requestHeaders: {</div>
    <div v-for="key in Object.keys(jsonLog.requestHeaders)" class="code-text" style="margin-left: 1em;">
      <span v-html="$options.filters.highlight(key, $store.state.search)"></span>
      <span>:</span><span style="margin-left: 0.5em"></span>
      <span v-html="$options.filters.highlight(jsonLog.requestHeaders[key], $store.state.search)"></span>
    </div>
    <div class="code-text">}</div>
    <div class="code-text">statusCode:
      <span v-html="$options.filters.highlight(jsonLog.statusCode, $store.state.search)"></span>
    </div>
    <div class="code-text">responseHeaders: {</div>
    <div v-for="key in Object.keys(jsonLog.responseHeaders)" class="code-text" style="margin-left: 1em;">
      <span v-html="$options.filters.highlight(key, $store.state.search)"></span>
      <span>:</span><span style="margin-left: 0.5em"></span>
      <span v-html="$options.filters.highlight(jsonLog.responseHeaders[key], $store.state.search)"></span>
    </div>
    <div class="code-text">}</div>
    <div class="code-text">bodyLength: {{jsonLog.bodyLength}}</div>
    <div class="code-text">responseBody:</div>
    <div class="code-text" style="margin-left: 1em;">{{jsonLog.responseBody}}</div>
  </sui-segment>
</template>

<script>
import timeAgo from '../timeAgo'

export default {
  name: 'HttpLog',
  props: {
    log: Object,
    index: Number
  },

  computed: {
    jsonLog() {
      return JSON.parse(this.log.message)
    },

    responseBody() {
      try {
        return JSON.parse(this.jsonLog.responseBody)
      } catch (e) {
        return this.jsonLog.responseBody
      }
    }
  },

  methods: {
    formatTimestamp(log) {
      return timeAgo(log.timestamp * 1000)
    },

    fileName(filePath) {
      const array = filePath.split('/')
      const fileName = array[array.length - 1]
      const fileNameParts = fileName.split(".")
      return fileNameParts[0]
    },
  }
}
</script>

<style>
.code-text {
  font-size: 13px;
  white-space: pre-wrap;
  word-break: break-all;
  font-family: monospace,monospace;
}

.highlight {
  background-color: rgba(255, 255, 255, 0.2);
}

</style>