import Vue from 'vue'
import Vuex from 'vuex'
import { uuid } from 'uuidv4';
Vue.use(Vuex)

let loweredMessages = {}
const delay = ms => new Promise(res => setTimeout(res, ms));
const filterBySearch = (search) => l => {
    if (search.length === 0) return false
    //const reg = new RegExp(search, "i")
    const lowered = search.toLowerCase()
    const key = uuid()
    let mess = loweredMessages[key] || l.message.toLowerCase()
    loweredMessages[key] = mess
    return l.function.toLowerCase().includes(lowered) || mess.includes(lowered)
}

const time = [
    {text: "Time", key: 0, value: -1},
    {text: "1 minute", key: 1, value: 1},
    {text: "3 minutes", key: 2, value: 3},
    {text: "5 minutes", key: 3, value: 5},
    {text: "7 minutes", key: 4, value: 7},
    {text: "10 minutes", key: 5, value: 10}
]

export default new Vuex.Store({
    state: {
        enabledLogLevels: [],
        disabledLogLevels: [],
        search: null,
        searchFileName: null,
        time: -1,
        logs: [],
        fileNames: {},
        logLevels: {}
    },
    mutations: {
        logsReceived(state, logs) {
            state.fileNames = {}
            logs.forEach(l => {
                Vue.set(state.fileNames, l.fileName, l.fileName)
                Vue.set(state.logLevels, l.level, {key: l.level, value: l.level, text: l.level, color: l.color})
            })
            state.logs = logs.map(l => ({...l, uid: uuid()}))
        },

        logReceived(state, logs) {
            Vue.set(state.fileNames, logs.fileName, "")
            Vue.set(state.logLevels, logs.level, {key: logs.level, value: logs.level, text: logs.level, color: logs.color})
            Vue.set(state.logs, state.logs.length, {...logs, uid: uuid()})
        },

        enabledLogLevels(state, value) {
            state.enabledLogLevels = value
        },

        disabledLogLevels(state, value) {
            state.disabledLogLevels = value
        },

        updateTime(state, value) {
            state.time = value
        },

        updateSearch(state, value) {
            state.search = value
        },

        updateSearchFileName(state, value) {
            state.searchFileName = value.toLowerCase()
        },

        clearLogs(state) {
            state.fileNames = {}
            state.logs = []
            state.fileName = null
        }
    },
    actions: {},

    getters: {
        timeById: (state) => (id) => {
            return time.find(t => t.value === id)
        },

        filteredLogs: (state) => (fileName) => {
            const search = state.search
            let logs = [...state.logs]
            if (state.enabledLogLevels.length > 0) logs = logs.filter(l => state.enabledLogLevels.includes(l.level))
            if (state.disabledLogLevels.length > 0) logs = logs.filter(l => !state.disabledLogLevels.includes(l.level))
            if (fileName !== "any") logs = logs.filter(l => l.fileName === fileName)
            if (search) logs = logs.filter(filterBySearch(search))
            if (state.time !== -1) {
                const time = (new Date().getTime() - state.time * 60000)
                logs = logs.filter(l => l.timestamp * 1000 >= time)
            }
            return logs
        },

        fileNames(state) {
            const names = Object.keys(state.fileNames)
            if (!state.searchFileName) return names
            return names.filter(n => n.toLocaleLowerCase().includes(state.searchFileName))
        },

        filterByTime(state) {
            return time
        }
    },

    modules: {}
})
