<template>
  <div class="home">
    <VirtualList
        ref="virtualList"
        style="height: 100%; overflow-y: auto;"
        :data-key="'uid'"
        :data-sources="logs"
        :data-component="itemComponent"/>
  </div>
</template>

<script>

import timeAgo from '../timeAgo'
import HttpLog from "../components/HttpLog";
import Log from "../components/Log";
import VirtualList from '../components/vue-virtual-scroll-list'

export default {
  name: 'Home',
  components: {
    HttpLog,
    Log,
    VirtualList
  },

  computed: {
    logs() {
      // setTimeout(window.scrollToBottom, 200)
      //const level = this.$route.params.logLevel || "verbose"
      this.$nextTick(() => {
        this.$refs.virtualList?.scrollToBottom()
      })
      const fileName = this.$route.query.fileName || "any";
      return this.$store.getters.filteredLogs(fileName)
    }
  },

  data() {
    return {
      itemComponent: Log,
      intervalId: null
    }
  },

  methods: {

    fileName(filePath) {
      const array = filePath.split('/')
      const fileName = array[array.length - 1]
      const fileNameParts = fileName.split(".")
      return fileNameParts[0]
    },

    formatTimestamp(log) {
      return timeAgo(log.timestamp * 1000)
    },
  }
}
</script>

