<template>
  <div class="log-wrapper">
    <HttpLog :log="source" v-if="source.level === 'http'" :index="index"/>
    <sui-segment
        v-if="source.level !== 'http'"
        :style="`border-left: 5px solid ${source.color} !important;`"
        :class="`${index % 2 === 0 ? 'even' : 'odd'}`"
    >
      <div>
        <time>{{ formatTimestamp(source) }}</time>
        <span class="logFileName">{{ fileName(source.fileName) }}</span>
        <span class="logFunction">.
            <span v-html="$options.filters.highlight(source.function, $store.state.search)"></span>
          </span>
        <span class="logLine"> :{{ source.line }}</span>
      </div>
      <div v-if="source.message.length> 0" v-html="$options.filters.highlight(source.message, $store.state.search)"></div>
    </sui-segment>
  </div>
</template>

<script>
import timeAgo from "../timeAgo";
import HttpLog from "../components/HttpLog";

export default {
  props: {
    source: Object,
    index: { // index of current item
      type: Number
    }
  },

  components: {HttpLog},

  methods: {
    formatTimestamp(log) {
      return timeAgo(log.timestamp * 1000)
    },

    fileName(filePath) {
      const array = filePath.split('/')
      const fileName = array[array.length - 1]
      const fileNameParts = fileName.split(".")
      return fileNameParts[0]
    },
  }
}
</script>