import Vue from "vue"

let socket = null

const emitter = new Vue({
    methods: {
        send(message) {
            if (socket === null || socket.readyState !== 1) return;
            socket.send(message)
        },

        reconnect() {
            if (socket == null) return;
            socket.close()
        }
    }
})

const onmessage = async (msg) => {
    const data = JSON.parse(await msg.data.text())
    emitter.$emit(data.type, data.data)
}

const onerror = (err) => {
    socket.close()
    socket = null;
    emitter.$emit('socket:error')
}

const onclose = (e) => {
    console.log('Socket is closed. Reconnect will be attempted in 1 second.', e.reason);
    socket = null;
    setTimeout(connect, 1000);
    emitter.$emit('socket:close')
};

const onopen = (e) => {
    console.log('Socket is opened');
    emitter.$emit('socket:connected')
};

const connect = () => {
    emitter.$emit('socket:connecting')
    let url
    const hostParts = location.host.split(":")
    if (hostParts.length > 1) {
        url = `${hostParts[0]}:8088`
    } else {
        url = location.host
    }
    if (socket !== null && socket.readyState === 1) return;
    socket = new WebSocket(`ws://${url}/ws`)
    socket.onerror = onerror
    socket.onmessage = onmessage
    socket.onclose = onclose
    socket.onopen = onopen
}

connect()

export default emitter