      </div>

      <div :class="`ui button basic danger bottom`" @click.prevent="clearLogs">
        <i class="icon trash alternate"/>
      </div>
    </div>
    <div :class="`left-navigation ${menuVisible ? 'visible': ''}`">
      <div class="ui search">
        <input class="prompt" v-model="searchFileName">
      </div>
      <sui-segment>
        <router-link :to="{name: 'Home', query: {fileName: null}}" @click.native="menuVisible = false">Any</router-link>
      </sui-segment>
      <sui-segment v-for="(name) in fileNames">
