              <i class="circle outline icon"></i>
            </div>

            <div class="ui search">
              <input class="prompt" v-model="search">
            </div>
          </div>

          <div class="bottom-menu">
            <div style="display: flex; flex-direction: row;">
              <sui-dropdown
                  fluid
                  multiple
