                  :use-bg-color="true"
                  placeholder="Include log levels"
                  direction="downward"
                  selection
                  v-model="enabledLogLevel"
              />
              <sui-dropdown
                  fluid
                  multiple
                  :options="disabledLevels"
                  :use-bg-color="true"
                  placeholder="Exclude log levels"
                  direction="downward"
                  selection
                  v-model="disabledLogLevels"
              />
            </div>

