                  name="address"
                  v-model="address"
                  placeholder="Address">
            </div>
          </div>
          <div class="field">
            <button
                :disabled="address === null"
                :class="`ui fluid large submit button`"
                @click.prevent="setAddress">Set</button>
          </div>
        </div>
      </div>
    </div>
