<script>
import Socket from "./Socket";

export default {
  name: 'App',
  data: () => ({
    menuVisible: false,
    alwaysOnTop: false,
    isSettingsOpened: false,
    isConnecting: false,
    isConnectError: false,
    address: localStorage.getItem('serverUrl') || "localhost:8088",
    l: null
  }),

  created() {
    Socket.$on('logs', this.logsReceived)
    Socket.$on('log', this.logReceived)
    Socket.$on('socket:connecting', this.socketConnecting)
    Socket.$on('socket:connected', this.socketConnected)
    Socket.$on('socket:error', this.socketConnectError)
  },

  beforeDestroy() {
    Socket.$off('logs', this.logsReceived)
    Socket.$off('log', this.logReceived)
    Socket.$off('socket:connecting', this.socketConnecting)
    Socket.$off('socket:connected', this.socketConnected)
    Socket.$off('socket:error', this.socketConnectError)
  },

  methods: {
    socketConnecting() {
      this.isConnectError = false
      this.isConnecting = true
    },

    socketConnectError() {
      this.isConnecting = false
      this.isConnectError = true
    },

    socketConnected() {
      this.isSettingsOpened = false
      this.isConnecting = false
      this.isConnectError = false
    },


    logsReceived(json) {
      this.$store.commit('logsReceived', json)
    },

    logReceived(json) {
      this.$store.commit('logReceived', json)
    },

    clearLogs() {
      if (!confirm("Delete all logs?")) return;
      Socket.send('clearLogs')
      this.$store.commit('clearLogs')
    },

    toggleAlwaysOnTop() {
      if (window.isElectron) {
        this.alwaysOnTop = !this.alwaysOnTop;
        window.ipcRenderer.send('toggleAlwaysOnTop', this.alwaysOnTop)
      }
    },

    lC() {
      console.log("ldsds")
    },

    setAddress() {
      this.isConnecting = true
      this.isSettingsOpened = false
      localStorage.setItem('serverUrl', this.address)
      Socket.reconnect()
    }
  },

  watch: {
    l: (newL, oldL) => {
      console.log(newL, oldL)
    }
  },

  computed: {
    isElectron() {
      return window.isElectron
    },

    disabledLevels() {
      const enabled = this.enabledLogLevel;
      const levels = Object.values(this.$store.state.logLevels)
      return levels.filter(l => !enabled.includes(l.value))
    },

    enabledLevels() {
      const disabled = this.disabledLogLevels;
      const levels = Object.values(this.$store.state.logLevels)
      return levels.filter(l => !disabled.includes(l.value))
    },

    enabledLogLevel: {
      get() {
        return this.$store.state.enabledLogLevels
      },
      set(value) {
        this.$store.commit('enabledLogLevels', value)
      }
    },

    disabledLogLevels: {
      get () { return this.$store.state.disabledLogLevels },
      set(value) {
        this.$store.commit('disabledLogLevels', value)
      }
    },

    time: {
      get() {
        const id = this.$route.query.time || -1
        const t = this.$store.getters.timeById(parseInt(id))
        console.log(t)
        return t.value
      },
      set(value) {
        const query = this.$route.query || {logLevel: null, search: null};
        this.$router.replace({name: "Home", query: {...query, time: value}});
        this.$store.commit('updateTime', value)
      }
    },

    search: {
      get() {
        return this.$store.state.search
      },
      set(value) {
        this.$store.commit('updateSearch', value)
      },
    },

    searchFileName: {
      get() {
        return this.$store.state.searchFileName
      },
      set(value) {
        this.$store.commit('updateSearchFileName', value)
      },
    },

    fileNames() {
      return this.$store.getters.fileNames;
    },

    filterByTime() {
      return this.$store.getters.filterByTime;
    },

    logLevels() {
      return Object.values(this.$store.state.logLevels)
    },
  }
}
</script>

