import {SemanticUIVueMixin} from '../../lib';
import Image from '../../elements/Image/Image';
import Icon from '../../elements/Icon/Icon';
import Label from '../../elements/Label/Label';

export default {
    name: 'SuiDropdownItem',
    mixins: [SemanticUIVueMixin],
    props: ['icon', 'label', 'text', 'subtitle', 'value', 'active', 'selected', 'disabled'],

    events: {
        select: {
            custom: true,
        },
    },

    methods: {
        select() {
            this.$emit('select', this.value);
        },

        renderText() {
            if (!this.subtitle) return this.text || this.$slots.default;
            return (
                <div>
                    <p>{this.text}</p>
                    <p>{this.subtitle}</p>
                </div>
            )
        },
    },

    render() {
        const ElementType = this.getElementType();
        return (
            <ElementType
                {...this.getChildPropsAndListeners()}
                role="option"
                class={this.classes(
                    this.disabled && 'disabled',
                    this.active && 'active',
                    this.selected && 'selected',
                    'item',
                )}
                onClick={this.select}
            >
                {this.icon && <Icon name={this.icon}/>}
                {this.image && this.image.length > 0 && <Image {...{props: this.image}} />}
                {this.label && <Label {...{props: this.label}} />}
                {this.renderText()}
            </ElementType>
        );
    },
    meta: {
        parent: 'SuiDropdown',
    },
};
